e there was so much uncertainty, different reports were coming in from the field and phone communication was intermittent. The one constant was that everyone who was asking for help was asking for some kind of shelter or cover from the elements for people who had lost their homes. Tents, tarpaulins, and corrugated iron sheets were in very short supply but we worked quickly to secure more than 1300 tarpaulins: including 400 donated by UNHCR; and about 375 donated by our own Rotary International District 3292.")]),_c('p',{staticClass:"text-justify"},[_vm._v("In the three weeks following the earthquake, we distributed tents and tarpaulins to affected districts as follows: Sindhupalchowk: 450; Gorkha: 350; Kavre: 170; Nuwakot: 94; Makwanpur: 50; Kathmandu: 75; and Dhading: 100. Most of these items, together with 150 solar lamps (donated by UNHCR), were delivered by truck, while some transport was donated, in most cases RC KTM Mid-Town paid for the transport. In one case, where the roads were blocked and we heard the situation was dire, we delivered some 150 tents to the most affected areas in Sindupalchowk by helicopter. See full details in First Earthquake Relief Assistance Report, May 2015.")]),_c('a',{attrs:{"href":"/images/our-projects/first-report.pdf","target":"_blank"}},[_vm._v("See Detailed Report")])]),_c('div',{staticClass:"row px-3 mt-2"},[_c('div',{staticClass:"col-md-3 text-center"},[_c('img',{staticClass:"img-fluid",attrs:{"src":"/images/our-projects/Immediate-relief-1.jpg","alt":""}})]),_c('div',{staticClass:"col-md-3 text-center"},[_c('img',{staticClass:"img-fluid",attrs:{"src":"/images/our-projects/Immediate-relief-2.jpg","alt":""}})]),_c('div',{staticClass:"col-md-3"},[_c('img',{staticClass:"img-fluid",attrs:{"src":"/images/our-projects/Immediate-relief-3.jpg","alt":""}})]),_c('div',{staticClass:"col-md-3"},[_c('img',{staticClass:"img-fluid",attrs:{"src":"/images/our-projects/Immediate-relief-4.jpg","alt":""}})])])])])},function () {var _vm=this;var _h